@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

input,textarea{width:100%;display:block}

body,h1,h2,h3,h4,p,
ul[class],
ol[class],
li,
figure,figcaption,blockquote,
dl,
dd {
  margin: 0;
}

li {
    list-style: none;
}

ul {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: white;
}
/* END OF RESET */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.quoteContainer {
  height: 100vh;
  display: flex;
  align-items: center;
}

:root {
    --main-color: #27AE60;
}

body {
    background-color: #27AE60;
    background-color: var(--main-color);
    transition: background-color 2s;
}

/* GLOBAL STYLE */
a {
    text-decoration: none;
}

/* MAIN CONTAINER */
.container {
    margin: 0 auto;
    max-width: 551px;
    height: auto;
    text-align: center;
    padding: 20px;
}

/* QUOTE BAR */
.reactQuoteBar {
    display: grid;
    grid-template-columns: 1f 1fr;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
    padding: 40px;
}

/* QUOTE */
.fullQuote {
    font-family: 'Dancing Script', cursive;
}

.quoteLeft {
    display: inline-block;
    font-size: 26px;
    color: #27AE60;
    color: var(--main-color);
    transform: translateY(-12px);
    margin-right: 5px;
    transition: color 2s;
}

.quote {
    text-align: center;
    font-size: 38px;
    color: #27AE60;
    color: var(--main-color);
    transition: color 2s;
}

.author {
    margin-top: 20px;
    color:  #27AE60;
    color:  var(--main-color);
    text-align: end;
    font-size: 34px;
    margin-bottom: 10px;
    transition: color 2s;
}

/* BOTTOM SECTION */
.bottomSection{
    display: flex;
    justify-content: space-between;

    align-items: center;
}

/* SOCIAL */
.social {
    display: flex;
}

/* NEW QUOTE BUTTON */
.newQuoteButton {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1em;
    background-color: #27AE60;
    background-color: var(--main-color);
    border: none;
    border-radius: 5px;
    padding: 10px;
    width: auto;
    color: white;
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: background-color 2s;
    flex-grow: 0;
}


/* TWITTER LINK */
.twitter {
    width: 40px;
    height: 40px;
    transform: translateY(-10px);
    font-size: 44px;
    margin-right: 5px;
}

.twitter a {
    color: #27AE60;
    color: var(--main-color);
    transition: color 2s;
}

/* TUMBLR LINK */
.tumblr {
    width: 40px;
    height: 40px;
    transform: translateY(-10px);
    font-size: 44px;
}

.tumblr a {
    color: #27AE60;
    color: var(--main-color);
    transition: color 2s;
}

/* PROJECT AUTHOR */
.projectAuthor {
    padding-top: 10px;
    color: white;
}

.tumblr:hover,
.twitter:hover,
.newQuoteButton:hover {
    opacity: .9;
} 
